import { clsx, type ClassValue } from "clsx"
import { twMerge } from "tailwind-merge"

export function cn(...inputs: ClassValue[]) {
  return twMerge(clsx(inputs))
}

// Validation helpers for fingerprint generation
export function validateFingerprintConsistency(fingerprint: any): string[] {
  const errors: string[] = [];

  // Check if User-Agent matches OS and browser selections
  if (fingerprint.userAgent && fingerprint.os && fingerprint.browser) {
    const userAgent = fingerprint.userAgent.toLowerCase();
    const os = fingerprint.os.toLowerCase();
    const browser = fingerprint.browser.toLowerCase();

    if (os.includes('windows') && !userAgent.includes('windows')) {
      errors.push('User-Agent does not match selected Windows OS');
    }
    if (os.includes('mac') && !userAgent.includes('mac')) {
      errors.push('User-Agent does not match selected macOS');
    }
    if (browser.includes('chrome') && !userAgent.includes('chrome')) {
      errors.push('User-Agent does not match selected Chrome browser');
    }
  }

  // Check screen resolution consistency with device type
  if (fingerprint.screenResolution && fingerprint.device) {
    const resolution = fingerprint.screenResolution;
    const device = fingerprint.device.toLowerCase();
    
    if (device === 'mobile' && !isMobileResolution(resolution)) {
      errors.push('Screen resolution does not match mobile device type');
    }
    if (device === 'desktop' && isMobileResolution(resolution)) {
      errors.push('Screen resolution does not match desktop device type');
    }
  }

  return errors;
}

function isMobileResolution(resolution: string): boolean {
  const mobileResolutions = [
    '375x667', '414x896', '360x640', '375x812', '414x736',
    '320x568', '360x780', '393x851', '412x915'
  ];
  return mobileResolutions.includes(resolution);
}

// Format fingerprint data for display
export function formatFingerprintForDisplay(fingerprint: any): Record<string, any> {
  const formatted: Record<string, any> = {};
  
  Object.entries(fingerprint).forEach(([key, value]) => {
    if (typeof value === 'object' && value !== null) {
      formatted[key] = JSON.stringify(value, null, 2);
    } else {
      formatted[key] = String(value);
    }
  });

  return formatted;
}

// Generate a random MAC address in valid format
export function generateRandomMacAddress(): string {
  const hexChars = '0123456789ABCDEF';
  let mac = '';
  
  for (let i = 0; i < 6; i++) {
    if (i > 0) mac += ':';
    mac += hexChars[Math.floor(Math.random() * 16)];
    mac += hexChars[Math.floor(Math.random() * 16)];
  }
  
  return mac;
}

// Generate realistic canvas hash
export function generateCanvasHash(): string {
  const chars = '0123456789abcdef';
  let hash = '';
  for (let i = 0; i < 32; i++) {
    hash += chars[Math.floor(Math.random() * chars.length)];
  }
  return hash;
}

// Format error messages for user display
export function formatErrorMessage(error: string, details?: string): string {
  let message = error;
  
  if (details) {
    if (details.includes('API key')) {
      message = 'API configuration error. Please check your settings.';
    } else if (details.includes('rate limit')) {
      message = 'Rate limit exceeded. Please try again in a few minutes.';
    } else if (details.includes('network') || details.includes('fetch')) {
      message = 'Network error. Please check your connection and try again.';
    }
  }
  
  return message;
}

// Copy text to clipboard with fallback
export async function copyToClipboard(text: string): Promise<boolean> {
  try {
    if (navigator.clipboard && window.isSecureContext) {
      await navigator.clipboard.writeText(text);
      return true;
    } else {
      // Fallback for older browsers
      const textArea = document.createElement('textarea');
      textArea.value = text;
      textArea.style.position = 'fixed';
      textArea.style.left = '-999999px';
      textArea.style.top = '-999999px';
      document.body.appendChild(textArea);
      textArea.focus();
      textArea.select();
      const result = document.execCommand('copy');
      textArea.remove();
      return result;
    }
  } catch (err) {
    console.error('Failed to copy text:', err);
    return false;
  }
}
